import React from 'react';

function TodoItemControls({ id, value, done, deleteTodo, editTodo }) {
  return (
    <div className="TodoItem__controls">
      <button className="TodoItem__button TodoItem--edit" onClick={e => editTodo(id, "Edited", done)}>
        <img className="TodoItem__icon" src="svg/edit.svg" alt="delete" />
      </button>
      <button className="TodoItem__button TodoItem--trash" onClick={e => deleteTodo(id)}>
        <img className="TodoItem__icon" src="svg/trash.svg" alt="delete" />
      </button>
    </div>
  );
}

export default TodoItemControls;
