import React, {Component, Fragment} from 'react';

class TodoItemEdit extends Component {

  state = {
    value: ''
  }

  constructor(props) {
    super();
    this.state.value = props.value;
  }

  render() {
    return (
      <Fragment>
        <input className="TodoItem__input" type="text"
          value={this.state.value}
          onKeyPress={this.handleKeyPress}
          onChange={this.handleEdit}
        />
        <button className="TodoItem__button TodoItem__button--edit"
          onClick={(e) => { this.handleDone(e) }}>
            <img className="TodoItem__icon" src="svg/check.svg" alt="icon" />
        </button>
      </Fragment>
    )
  }
  
  handleEdit = (e) => {
    this.setState({
      value: e.target.value
    });
  }

  handleKeyPress = (e) => {
    if(e.key === 'Enter') {
      this.handleDone(e);
    }
  } 
  
  handleDone = (e) => {
    const { id, done } = this.props;
    if(this.state.value) {
      this.props.changeMode('VIEW');
      this.props.editTodo(id, this.state.value, done);
    } else {
      alert('Пустое нельзя');
    }
  }

}

export default TodoItemEdit;
