import React, { Component } from 'react';
import TodoItemEdit from './TodoItemEdit';
import TodoItemView from './TodoItemView';
import './TodoItem.css';

class TodoItem extends Component {

  state = {
    mode: 'VIEW',
  }

  constructor(props) {
    super();
    this.state.value = props.value;
  }

  render() {
    let currentView;
    const { value, done, id, deleteTodo, editTodo } = this.props;

    if (this.state.mode === 'VIEW') {
      currentView = <TodoItemView value={value} done={done} id={id} editTodo={editTodo} deleteTodo={deleteTodo} changeMode={this.changeMode}/>;
    } else if (this.state.mode === 'EDIT') {
      currentView = <TodoItemEdit value={value} done={done} id={id} editTodo={editTodo} changeMode={this.changeMode}/>;
    }

    return (
      <li className={`TodoItem ${done ? 'TodoItem--done' : ''}`}>
        {currentView}
      </li>
    );
  }

  changeMode = (mode) => {
    this.setState({ mode });
  }

}

export default TodoItem;