import React, { Component, Fragment } from 'react';
import TodoItemControls from './TodoItemControls';

class TodoItemView extends Component {
  
  render() {
    const { id, value, done, changeMode, deleteTodo, editTodo } = this.props;
    return (
      <Fragment>
        <div className="TodoItem__value" onClick={e => { editTodo(id, value, !done) }}>
          {value}
        </div>
        <TodoItemControls deleteTodo={deleteTodo} editTodo={() => { changeMode('EDIT') }} id={id} />
      </Fragment>
    )
  }
}

export default TodoItemView;