import React, { Component } from 'react';
import './App.css';
import TodoAdd from '../TodoAdd';
import TodoList from '../TodoList';


class App extends Component {

  state = {
    todos: [
      this.createTodo(`React TodoList`, true, Math.random()),
      this.createTodo(`Найти работу`, false, Math.random()),
      this.createTodo(`Сдохнуть`, false, Math.random())
    ]
  }

  constructor(props) {
    super();
  }

  render() {
    return (
      <div className="App">
        <TodoAdd addTodo={this.addTodo} />
        <TodoList todos={this.state.todos} deleteTodo={this.deleteTodo} editTodo={this.editTodo} />
      </div>
    );
  }

  addTodo = (value) => {
    this.setState((state) => {
      let todos = [...state.todos];
      todos.push(this.createTodo(value, false, Math.random()));
      return { todos };
    });
  }

  createTodo(value, done, id) {
    return {
      value,
      done,
      id
    }
  }

  editTodo = (id, value, done) => {
    this.setState((state) => {
      let todos = [...state.todos].map(item => {
        if(item.id === id)
          return this.createTodo(value, done, id);
        else
          return item;
      });

      return {
        todos
      };
    });
  }

  deleteTodo = (id) => {
    this.setState((state) => {
      let todos = [...state.todos];
      let indexOf = 0;
      todos.forEach((item, index) => {
        if (item.id === id) {
          indexOf = index;
        }
      });
      todos.splice(indexOf, 1);

      return {
        todos
      }
    });
  }
}

export default App;