import React, { Component } from 'react';
import './TodoAdd.css';

class TodoAdd extends Component {

  state = {
    value: ''
  }

  constructor(props) {
    super();
  }

  handleClick = (e) =>{
    if(this.state.value) {
      this.props.addTodo(this.state.value);
      this.setState(
        {
          value: ''
        }
      );
    } else {
      alert('Пустое нельзя');
    }
  }

  handleKeyPress = (e) => {
    if(e.key === 'Enter') {
      this.handleClick(e);
    }
  } 

  handleInput = (e) => {
    this.setState(
      {
        value: e.target.value
      }
    );
  }

  render() {
    return (
      <div className="TodoAdd">
        <input
          className="TodoAdd__input"
          placeholder="Напиши задачу"
          type="text"
          value={this.state.value}
          onChange={this.handleInput}
          onKeyPress={this.handleKeyPress} />
        <button
          className="TodoAdd__button"
          onClick={this.handleClick}>Добавить</button>
      </div>
    );
  }

}

export default TodoAdd;